body{
  background-color: #101115;
  padding: 50px 150px 0 150px;

  margin: 0;
}

@font-face {
    font-family: 'Circular Std';
    src: url(/tedora.info/static/media/CIRCULARSTD-MEDIUM.4fcdd97f.OTF);
}

/*          --TITLE---          */
.Title{
    padding: 70px 0;
    font-family: 'Circular Std';
    height: 400px;
}
.t_btn_contact{
    background-color: #3E82E5;
    color: white;
    width: 230px;
    height: 70px;
    border-radius: 7px;
    margin-top: 30px;
    font-weight: bold;
    font-size: large;
    cursor: pointer;
    border: #3E82E5 1px solid;
    box-shadow: 0;
    transition: box-shadow 0.5s;  
}
.t_btn_contact:hover{
    box-shadow: 2px 2px 70px #3E82E5;
}

/*      ---Carousel---      */

.Carousel{
    display: flex;
    margin: 0 auto;
    height: 320px;
}
.slide{
    width: 350px;
    height: 350px;
    background-color: #3E82E5;
    margin-right: 50px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0;
    transition: box-shadow 0.5s;  
    transition: all 0.5s;
}
.slide:hover{
    width: 370px;
    height: 370px;
    box-shadow: 2px 2px 30px #3E82E5;
}

.mere{
    width: 350px;
    height: 350px;
}

/*          --VALUES---          */
.Values{
    font-family: 'Circular Std';
    color: white;
    margin-top: 210px;
    position: static;
}
.value{
    display: flex;
}
.value_title{
    font-size: 37px;
    font-weight:  100px;
    color: #3E82E5;
    margin-left: 35px;
}
.v_left{
    padding-top: 70px;
    width: 400px;
}
.v_left p {
    font-weight: lighter;
}
@font-face {
    font-family: 'Circular Std';
    src: url(/tedora.info/static/media/CIRCULARSTD-MEDIUM.4fcdd97f.OTF);
}

.header_page{
    font-family: 'Circular Std';
    height: 90px;
    width: auto;
}
.options{
    float: right;
    justify-content: space-around;
    display: flex;
    list-style: none;
    margin-top: 10px;
    color: white;
    top: 50%;

}
.logo{
    float: left;
}
.opt_header{
   padding-left: 40px;
}
.hire_me{
    float: right;
    margin-left: 20px;
}
.hire_btn{
    display: flex;
    font-family: 'Circular Std';
    color: white;
    background-color: transparent;
    border: 1px white solid;
    border-radius: 5px;
    height: 35px;
    padding: 10px 15px; 
    cursor: pointer;

}
.hire_btn:hover{
    background-color: #3E82E5;
    color: white;
    border: 1px #3E82E5 solid;
}

